body.wp-admin {
  background-color: #dcdcde;
}

.acf-field-group,
.acf-field-repeater {
  background-color: #f0f0f1;
}
.acf-field-group .acf-label,
.acf-field-repeater .acf-label {
  font-size: 16px;
}
.acf-field-group + .acf-field-group > .acf-label {
  margin: 0;
  font-size: 1.6rem;
  padding: 1rem 2rem;
  border-radius: 6px 6px 0 0;
  color: color("black") !important;
  background-color: #ccc !important;
}

.acf-field[data-width] + .acf-field[data-width],
.acf-fields > .acf-field {
  border-color: #ccc !important;
}

.acf-field-wysiwyg[data-name=title] .acf-editor-wrap iframe {
  min-height: 100px !important;
  height: 100px !important;
}

.acf-field-wysiwyg[data-name=text] .acf-editor-wrap iframe,
.acf-field-wysiwyg[data-name=content] .acf-editor-wrap iframe {
  min-height: 240px !important;
  height: 240px !important;
}

.acf-gallery {
  height: 250px !important;
}

.acf-accordion-title {
  background-color: #eee !important;
}
.acf-accordion-title label {
  font-size: 32px !important;
  line-height: 1em !important;
}
.acf-accordion-title .description {
  font-size: 15px !important;
}

.acf-fields.-left > .acf-field:before {
  background-color: #e3dfdf !important;
}